/* App.css */
.App {
    text-align: center;
}

.App-header {
    background: linear-gradient(135deg, #3c475c, #5a6f8f);
    padding: 240px 20px;
    padding-bottom: 200px;
    margin-bottom: -140px;
    color: #ffffff;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    font-family: 'Arial', sans-serif;
}

.App-header h1 {
    margin: 0;
    font-size: 48px;
}

h1 {
    font-size: 2em;
    text-align: center;
}
#code_appendix{
    text-align: center;

}
#video{
    text-align: center;
}
h2 {
    font-size: 1.5em;
}

h3 {
    font-size: 1.2em;
}

.App-header p {
    margin: 10px 0;
    font-size: 24px;
}

.floating-nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #333;
    padding: 10px 0;
}

.floating-nav ul {
    list-style-type: none;
    margin: 0;
    padding: 10px;
    text-align: center;
}

.floating-nav li {
    display: inline;
}

.floating-nav li:not(:last-child) {
    margin-right: 20px;
}

.floating-nav a {
    color: rgb(255, 255, 255);
    padding-top:10px;
    padding-bottom:10px;
    text-decoration: none;
}

.floating-nav a:hover {
    text-decoration: underline;
}

img.responsive-img {
    max-width: 50%;
    height: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
}
img.responsive-img-budget {
    max-width: 50%;
    height: auto;
    display: block;
    margin-top: -20px;
    margin-left: auto;
    margin-right: auto;
}

[id]::before {
    content: '';
    display: block;
    height: 50px;
    margin-top: -60px;
    visibility: hidden;
}
#contribution {
    text-align: center;
}
main {
    margin: 130px;
    text-align: left;
}
figcaption {
    text-align: center;
}